*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    background-color:#4c3434;
    color:white;
}
.container{
    width:90%;
    margin:0 auto;
}
.nav{
    height:80px;
    background-color: #461b1b;
    border-bottom: 2px solid lightgrey;
}
.nav ul{
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.nav a{
    color:#e8d7bc;
    text-decoration: none;
    font-size: 1.5em;
}
.nav ul:last-child(){
    flex:1;
}